import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import { supabase } from "./../lib/supabaseClient.js";


Vue.use(Vuex)
// const proxyurl = "https://cors-anywhere.herokuapp.com/";
// const proxyurl = "https://cors.eu.org/";
// const proxyurl = "https://cors.io/";
// const proxyurl = "https://api.allorigins.win/raw?url=";
const proxyurl = "";
const mainUrl = "https://market.csgo.com/api";
const apiKey = "8b40w7zrq0Q9Z8iV3WS80j8FN8H9G62";


export default new Vuex.Store({
  state: {
    currentPeople:{},
    statusTakeItem: 1,
    inventoryList: [],
    itemsListNow: [],
    currentItemInfo: {},
    online: false,
    isItemsOnBot: false,
    itemsInBD:[],
  },
  getters: {
  },
  mutations: {
    MUTATE_CURRENT_PEOPLE(state, params){
      state.currentPeople = params ;
    },
    MUTATE_STATUS_TAKE_ITEM(state, params){
      state.statusTakeItem = params ;
    },
    MUTATE_INVENTORY(state, params){
      state.inventoryList = params ;
    },
    MUTATE_ONLINE(state, params){
      state.online = params ;
    },
    MUTATE_ITEMS_LIST_NOW(state, params){
      var tempArr = params.map((item, i, arr) => {
        const tempItemBD = state.itemsInBD.find(j => j.info1 == item.classid && j.info2 == item.instanceid);
        if (tempItemBD) {
          item.MIN_PRICE= tempItemBD.min_price;
          item.MAX_PRICE= tempItemBD.max_price;
          item.UPDATE_PRICE= tempItemBD.is_update;
        }
        return item;
      });
      state.itemsListNow = tempArr ;
    },
    MUTATE_ITEMS_INFO_BY_ID(state, params){
      var tempObj = params;
      var data = state.itemsInBD.find(j => j.info1 == params.classid && j.info2 == params.instanceid);
      // var data = JSON.parse(localStorage.getItem(`${params.classid+'_'+params.instanceid}`));
      if (data) {
        tempObj.minPrice = data.min_price;
        tempObj.maxPrice = data.max_price;
        tempObj.updatePrice = data.is_update;
      }
      state.currentItemInfo = tempObj ;
    },
    //Проверка есть ли предмет забрать/отдать
    MUTATE_CHECK_ITEMS_ON_BOT(state, params){
      state.isItemsOnBot = params ;
    },
    //паолучения списка предметов из БД
    MUTATE_ITEMS_IN_BD(state, params){
      state.itemsInBD = params ;
    },
    
  },
  actions: {
    //получение пользователя из suoabase
    async GET_PEOPLE({commit, state, dispatch}, params){
      var {data} = await supabase.from("people").select().eq('id', params);
      if (data.length > 0) {
       await commit('MUTATE_CURRENT_PEOPLE', data[0]);
       if (state.currentPeople.api) {
        //  await dispatch('PING_ONLINE'); 
       } else{
        return false
       }
       return true
      } else{
        return false
      }
    },
    //поддержка онлайна на ТМ
    PING_ONLINE({commit, state}){
      axios.get(proxyurl + mainUrl+ `/v2/ping?key=${state.currentPeople.api}`,{
        headers: {
            'x-requested-with': "XMLHttpRequest"
          }
        }
      )
      .then(response => {
        commit('MUTATE_ONLINE', response.data.online)
      })
      .catch(error => {
        console.log(error, 1);
      });
    },
    GET_ITEMS_INVENTORY({commit, state}){
      

      axios.get(proxyurl + mainUrl+ `/v2/my-inventory/?key=${state.apiKey}`,{
        headers: {
          'x-requested-with': "XMLHttpRequest"
        }
      }
    )
      .then(response => {
        commit('MUTATE_INVENTORY', response.data.items)
      })
      .catch(error => {
        // handle error
        console.log(error);
      });
    },

    async GET_ITEMS_LIST_NOW({commit, state, dispatch}){
      var {data} = await supabase.from("main_table").select().eq('id_people', state.currentPeople.id);
      await commit('MUTATE_ITEMS_IN_BD', data);
      console.log(state.itemsInBD);
      // return true;
      axios.get(proxyurl + mainUrl+ `/v2/items?key=${state.currentPeople.api}`,{
        headers: {
          'x-requested-with': "XMLHttpRequest"
        }
      }
    )
      .then(response => {
        commit('MUTATE_ITEMS_LIST_NOW', response.data.items)
      })
      .catch(error => {
        console.log(error);
        // setTimeout(() => {
        //   dispatch("GET_ITEMS_LIST_NOW");
        // }, 10000);
      });
    },

    GET_ITEMS_INFO_BY_ID({commit, state}, params){
      // console.log(params);
      // return;
      axios.get(proxyurl + mainUrl+ `/ItemInfo/${params.class}_${params.instance}/ru/?key=${state.currentPeople.api}`,{
        headers: {
          'x-requested-with': "XMLHttpRequest"
        }
      }
    )
      .then(response => {
        commit('MUTATE_ITEMS_INFO_BY_ID', response.data)

        // return response.data
        // commit('MUTATE_ITEMS_LIST_NOW', response.data)
      })
      .catch(error => {
        // handle error
        console.log(error);
      });
    },
    UPDATE_PRICE_ITEM({commit, state}, params) {
      // axios.get(proxyurl + mainUrl+`/BestSellOffer/${params.class}_${params.instance}/?key=${state.apiKey}`, {
      axios.get(proxyurl + mainUrl+`/SellOffers/${params.class}_${params.instance}/?key=${state.currentPeople.api}`, {
        headers: {
          'x-requested-with': "XMLHttpRequest"
        }
      })
      .then(response => {
        console.log(3, response);
        var changePrice = 0;
        var best_offer = Number(response.data.best_offer);
        var offers = response.data.offers;
        if (best_offer < params.min) {
          changePrice = params.max;
        } else{
          if(Number(offers[0].my_count) > 0){
            if (Number(offers[0].my_count) != Number(offers[0].count)) {
              changePrice = best_offer-1;
            } else {
              changePrice = best_offer;
            }
          } else{
            if (Number(offers[0].price) >= params.min) {
              changePrice = Number(offers[0].price)-1;
            } else{
              changePrice = params.max;
            }
          }
        } 
        if (changePrice!=params.price) {
          axios.post(proxyurl + mainUrl+`/MassSetPrice/${params.class}_${params.instance}/${changePrice}/?key=${state.currentPeople.api}`, {
            headers: {
              'x-requested-with': "XMLHttpRequest"
            }
          })
          .then(response => {
            return response.data.success;
          })
          .catch(error => {
            console.log(error);
            return false;
          });
        }      
        return true;
      })
      .catch(error => {
        console.log(error);
        return false;
      });
    },

    CHECK_ITEMS_ON_BOT({commit}){
      axios.get(proxyurl + mainUrl + `/v2/trades/?key=${apiKey}`,{
        headers: {
          'x-requested-with': "XMLHttpRequest"
        }
      }
    )
      .then(response => {
        commit('MUTATE_CHECK_ITEMS_ON_BOT', response.data.success)
      })
      .catch(error => {
        // handle error
        console.log(error);
      });
  
    },
    //сохранение цены в БД
    async ADD_ITEMS_IN_BD({commit, state},params){
      console.log(params);
      var {data} = await supabase.from("main_table").select().eq('info1', params.classid).eq('info2', params.instanceid);
      if (data.length>0) {
        var {data} = await supabase.from("main_table").update({min_price: Number(params.minPrice), max_price: Number(params.maxPrice), is_update: params.updatePrice}).eq('id', data[0].id);
      } else {
        var {data} = await supabase.from("main_table").insert({id_people: state.currentPeople.id, name: params.name, min_price: Number(params.minPrice), max_price: Number(params.maxPrice), is_update: params.updatePrice, info1: params.classid, info2: params.instanceid});
      }
      return true;
    },
  },
  modules: {
  }
})
