<template>
  <v-app >
    <v-app-bar app color="light" height="80" class="pt-2" dark>
      <v-container>

        <v-row>
          <v-col cols="3">
            <h2 class="warning--text">{{ currentPeople.name }}</h2>
          </v-col>
          <v-col cols="2">
            <v-switch v-model="online" @change="isOnlineUserInSite()" :true-value="online" :color="online ? 'success' : 'error'" :label="'Статус онлайна'"></v-switch>
          </v-col>
          <v-divider class="my-2" vertical></v-divider>
          <v-col cols="3" >
            <div class="d-flex">
              <v-text-field v-model="pApiKey" type="password" label="Введите ключ Api" dense hide-details></v-text-field>
              <v-btn @click="addApiKey()" color="success" icon large><v-icon>mdi-content-save-outline</v-icon></v-btn>
            </div>
          </v-col>
          <v-divider class="my-2" vertical></v-divider>
          <v-col cols="2">
            <v-btn @click="deleteSavePrice()" color="error" block disabled>Удалить все цены<v-icon>mdi-delete</v-icon></v-btn>
          </v-col>
          <v-divider class="my-2" vertical></v-divider>
          <v-col cols="2">
            <span>{{ statusTakeItem == 2 ? 'Отдай боту предмет' : statusTakeItem == 4 ? 'Забери предмет' : 'Нечего передавать / принимать'}}</span>
            <!-- <v-btn @click="checkItemsOnBot()" :loading="loadingCheck" :color="isItemsOnBot ? 'success' : 'error'">Проверка приема/передачи вещи</v-btn><br> -->
          </v-col>
          
        </v-row>
      </v-container>
    </v-app-bar>
    <v-main style="background: #1e1e1e">
      <router-view></router-view>
    </v-main>
  </v-app>
</template>

<script>

import { mapState, mapActions, mapMutations } from 'vuex';
// import API_KEY from './db.json'
import { supabase } from "./lib/supabaseClient.js";

  export default {
    name: 'App',

    data() {
      return {
        pApiKey: '',
        loadingCheck: false,
        // isOnline: false,
        };
    },
    computed:{
      ...mapState(["online", "isItemsOnBot", "statusTakeItem", "currentPeople"])
    },
    mounted(){
      this.getApiPeolpe()
    },  
    methods:{
      ...mapActions(["GET_PEOPLE", "GET_API", "PING_ONLINE", "CHECK_ITEMS_ON_BOT"]),
      ...mapMutations([""]),

      async getApiPeolpe(){
        var checkPeople = await this.GET_PEOPLE(Number(this.$route.query.id));
        if(!checkPeople){
          alert('Ошибка c пользователем или нет API');
          return;
        }
        this.pApiKey = this.currentPeople.api;
        //  this.isOnlineUserInSite();
      },

      isOnlineUserInSite(){
        setInterval(() => {
          this.PING_ONLINE();
        }, 180000);
      },  
      
      async addApiKey(){
        try {
          const { error } = await supabase.from('people').update({ api: this.pApiKey }).eq('id', Number(this.$route.query.id));
          if (error) {
            throw error;
          }
        } catch (error) {
          alert(error)
        }
      },
      deleteSavePrice(){
        localStorage.clear();
        localStorage.setItem('API_KEY',this.pApiKey);

      },  
    }
};
</script>
