import Vue from 'vue'
import VueRouter from 'vue-router'
import NewItemView from '../views/NewItemView'
import OldItemView from '../views/OldItemView'

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/home' },
  {
    path: '/home',
    name: 'home',
    component: NewItemView
  },
  {
    path: '/olditem',
    name: 'olditem',
    component: OldItemView
  }
]

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
})

export default router
