<template>
  <v-container >
   <v-data-table :items="itemsListNow" :loading="loadingTable" :items-per-page="-1" dense dark>
    <template #[`top`]>
      <v-row class="my-4">
        <v-btn @click="getItemsListNow()" class="ma-4" color="primary" dense>Предметы на продаже</v-btn>
        <v-btn v-if="itemsListNow.length > 0" @click="updatePriceItems()" class="ma-4" :color="updateItem ? 'success' : 'warning'" dense>Обновлять цены</v-btn>
        <v-spacer></v-spacer>
        <v-btn  @click="openAutoRequest()" class="ma-4" color="primary" dense>Автопокупка</v-btn>
      </v-row>
      <v-dialog v-model="dialogEdit" max-width="700">
        <v-card>
          <v-card-title class="primary white--text" style="border-radius: 0 0 10px 10px ">Редактировнаие предмета</v-card-title>
          <v-divider class="my-4 mx-3"></v-divider>
          <v-card-text>
            <v-row>
              <v-col cols="12">
                <v-switch v-model="currentItemInfo.updatePrice" :color="currentItemInfo.updatePrice ? 'success' : 'error'" label="Отслеживать изменение цены" hide-details dense></v-switch>
              </v-col>
              <v-col cols="12">
                <v-text-field v-model="currentItemInfo.name" label="Название предмета" readonly dense hide-details></v-text-field>
              </v-col>
              <v-col cols="5">
                <v-text-field v-model="currentItemInfo.quality" label="Качество" readonly dense hide-details></v-text-field>
              </v-col>
              <v-col cols="5">
                <v-text-field v-model="currentItemInfo.rarity" label="Раритетность" readonly dense hide-details></v-text-field>
              </v-col>
              <v-col cols="2">
                <v-text-field :value="currentItemInfo.stickers ? 'Есть' : 'Нет'" label="Наклейки" readonly dense hide-details></v-text-field>
              </v-col>
              <v-col cols="4">
                <v-text-field v-model="currentItemInfo.minPrice" label="Минимальная цена (Твоя)" dense type="number" hide-details></v-text-field>
              </v-col>
              <v-col cols="4">
                <v-text-field :value="currentItemInfo.min_price" label="Минимальная цена (Маркет)" readonly dense hide-details></v-text-field>
              </v-col>
              <!-- <v-col cols="6">
                <v-text-field v-model="currentItemInfo.currentPrice" label="Твоя цена текущая" readonly dense hide-details></v-text-field>
              </v-col> -->
              <v-col cols="4">
                <v-text-field v-model="currentItemInfo.maxPrice" label="Твоя максимальная цена" dense type="number" hide-details></v-text-field>
              </v-col>
            </v-row>
            <span class="error--text mt-4">Цены в копейках</span>
            <!-- <pre>{{ currentItemInfo }}</pre> -->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn @click="saveEditItem()"  :loading="loadingTable" color="success">Сохранить</v-btn>
            <v-btn @click="dialogEdit = false" color="secondary">Закрыть</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </template>
    <template #[`header`]>
      <tr >
        <th style="width: 50px">Действия</th>
        <th>№</th>
        <th>Статус</th>
        <th>Название</th>
        <th style="width: 150px">Min</th>
        <th style="width: 150px">Твоя цена</th>
        <th style="width: 150px">Max</th>
        <th>Позиция</th>
      </tr>
    </template>
    <template #[`item`]="{item, index}">
      <tr @dblclick="edititem(item)">
        <td class="text-center">
          <div class="d-flex align-center" >
            <v-checkbox v-model="item.UPDATE_PRICE" :color="item.UPDATE_PRICE ? 'error' : ''" readonly></v-checkbox>
            <v-btn @click="edititem(item)" color="blue" icon><v-icon>mdi-pencil</v-icon></v-btn>
          </div>
        </td>
        <td class="text-center">{{ index+1}}</td>
        <td class="text-center">{{ 
          item.status == 1 ? 'На продаже' :
          item.status == 2 ? 'Продана' : 
          item.status == 3 ? 'Куплена' : 'Забрать' 
        }}</td>
        <td class="text-center">{{ item.market_hash_name }}</td>
        <td class="text-center">{{ item.MIN_PRICE | filterCostTable }}</td>
        <td class="text-center">{{ item.price }}</td>
        <td class="text-center">{{ item.MAX_PRICE | filterCostTable }}</td>
        <td class="text-center" :style="{background:item.position < 10 ? '#4caf50' : '#fb8c00' }">{{ item.position }}</td>
      </tr>
    </template>

   </v-data-table>
  
   <!-- Звук передачи предмета -->
   <audio ref="audio" src="../assets/message1.mp3"></audio>

  </v-container>
</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex';
  export default {
    name: 'NewItem',
    // watch:{
    //   online(to, from){
    //     console.log(to, from);
    //     if(to){
    //       this.PING_ONLINE();
    //     }
    //   }
    // },

    data() {
      return {
        loadingTable: false,
        currentItem:{},
        dialogEdit: false,
        tempArr: [],
        updateItem: false,
      };
    },
    props:{
      idPeople:{
        type: Number,
        default: null,
      }
    },    

    computed:{
      ...mapState(["sum", "inventoryList", "isItemsOnBot", 'itemsListNow', "currentItemInfo"]),

    },
    filters:{
      filterCostTable(val){
        if (val) {
          return val/100;
        } else{
          return 0;
        }
      }
    },  
    mounted(){
      console.log(this.idPeople);
      // data = await this.GET_ITEMS_INFO_BY_ID({class: this.itemsListNow[i].classid, instance: this.itemsListNow[i].instanceid});
            // if (data) {
              
            //   this.itemsListNow[i].market_hash_name = data.market_name;
            //   this.$set(this.itemsListNow[i], 'CURRENT_PRICE', data.min_price);
            // }
        


    },  
    methods:{
      async add(){
        var data = await this.GET_ITEMS_INFO_BY_ID({class: 5268244156, instance: 188530139});
        console.log(data);
        var data1 = await this.GET_ITEMS_INFO_BY_ID({class: 5268244156, instance: 188530139});
        console.log(data1);
        
      },  
      ...mapActions(['GET_ITEMS_INVENTORY', 'ADD_ITEMS_IN_BD', 'CHECK_ITEMS_ON_BOT', 'GET_ITEMS_LIST_NOW', "GET_ITEMS_INFO_BY_ID", "UPDATE_PRICE_ITEM"]),
      ...mapMutations(["MUTATE_STATUS_TAKE_ITEM", "MUTATE_ITEMS_INFO_BY_ID"]),
      /*------------------------| ОТКРЫТИЕ ОКНА ПРЕДМЕТА |-------------------------------- */ 
    ///////////////////////////////////////////////////////////////////////////////////////  
      async edititem(pItem){
        this.loadingTable = true;
        this.dialogEdit = true;
        this.MUTATE_ITEMS_INFO_BY_ID({});
        
        await this.GET_ITEMS_INFO_BY_ID({class: pItem.classid, instance: pItem.instanceid});
        this.loadingTable = false;
      },
      async saveEditItem(){
        const tempAdd = await this.ADD_ITEMS_IN_BD(this.currentItemInfo);
        if (tempAdd) {
          this.dialogEdit = false;
          this.getItemsListNow();
        }
        // localStorage.setItem(`${this.currentItemInfo.classid+'_'+this.currentItemInfo.instanceid}`,JSON.stringify({min: this.currentItemInfo.minPrice, max: this.currentItemInfo.maxPrice, updatePrice: this.currentItemInfo.updatePrice}));
      },

      async getItemsListNow(){
        this.loadingTable = true;
        this.GET_ITEMS_LIST_NOW();
        setTimeout(() => {
          var pStatus2 = this.itemsListNow.some(item => item.status == 2);
          var pStatus4 = this.itemsListNow.some(item => item.status == 4);
          this.$refs.audio.volume = 0.4;
          if (pStatus2) {
            this.$refs.audio.play();  
            this.MUTATE_STATUS_TAKE_ITEM(2);
          }
          if (pStatus4) {
            this.$refs.audio.play();
            this.MUTATE_STATUS_TAKE_ITEM(4);
          }
          this.loadingTable = false;
        }, 6000);

      },
     async updateMassPrice(){
      this.tempArr = []
        for (const item of this.itemsListNow) {
          this.updatePriceItem(item);
        }
        console.log(2, this.tempArr);
        setTimeout(() => {
          this.getItemsListNow();
        }, 4000);
      },
      async updatePriceItems(){
        if (!this.updateItem) {  
          this.updateItem = true;
          this.updateMassPrice();
          setInterval(() => {
            this.updateMassPrice();
            
          }, 60000);
        }
      },
      async updatePriceItem(pItem){
        if (this.tempArr.findIndex( item => item == `${pItem.classid}_${pItem.instanceid}`) == -1) {
          this.tempArr.push(`${pItem.classid}_${pItem.instanceid}`)
          if (pItem.UPDATE_PRICE && pItem.MIN_PRICE) {
            console.log(1, pItem);
            await this.UPDATE_PRICE_ITEM(
            {
              min: pItem.MIN_PRICE, 
              max: pItem.MAX_PRICE, 
              itemId: pItem.item_id, 
              class: pItem.classid, 
              instance: pItem.instanceid, 
              price: pItem.price*100
            });
          }
        }
      },
    }
    

  }
</script>
